@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900&display=swap);
body {
  margin: 0;
  font-family: "Lato";
  text-decoration: none;
}

.cointenerMenu {
  background: #0d0d0d;
  width: 20%;
  padding: 50px 20px 20px 20px;
  margin: 0 auto;

}
.logoMenu {
  width: 40%;
  margin: auto;
  padding-bottom: 35px;
}
.fa-bars {
  display: none;
}
.cointenerButtons {
  text-decoration: none;
}
.buttonMenu {
  color: #b3b3b3;
  margin: auto;
 /* margin: 50px 0 0 80px;  */
  font-family: "Lato";
  font-size: 18px;
  text-align: center;
  width: 50%;
  cursor: pointer;
  padding-bottom: 35px;
}
.buttonMenu:hover {
  color: #00fa77;
}

@media only screen and (max-width: 1110px) {
  .buttonMenu {
    color: #b3b3b3;
    margin: auto;
    /* margin: 50px 0 0 80px; */
    font-family: "Lato";
    font-size: 18px;
    text-align: center;
    width: 100%;
    cursor: pointer;
}
.logoMenu {
  width: 60%;
  padding-bottom: 15px;
}
}

@media only screen and (max-width: 992px) {
  .logoMenu {
    width: 65%;
    padding-bottom: 15px;
  }
}

@media only screen and (max-width: 768px) {

  .logoMenu {
    width: 50%;
  }
  .cointenerMenu {
    width: 30%;
  }
}

@media only screen and (max-width: 650px) {
  .cointenerMenu {
    height: 80px;
    width: 100%;
    padding: 10px 0;
    overflow: hidden;
  }
  .containerLogoBurger {
    display: flex;
  }
  .logoMenu {
    width: 33%;
    padding: 0;
  }
  .cointenerButtons {
    background: #00fa77;
    height: auto;
    padding: 15px;
  }
  .buttonMenu {
    color: #0d0d0d;
    font-family: "Lato";
    font-size: 16px;
    width: 100%;
    cursor  : pointer;
  }
  .fa-bars {
    display: inline-block;
    color: #00fa77;
    text-align: right;

  }
}
.cointenerMain {
    background-color: #171617;
    display: flex;
    height: 100vh;
}
.titleMain{
    width: 80%;
    color: cornsilk;
    padding: 40px;
    font-size: 20px;
    font-family: "Lato";
}
.cointenerAbout {
  background-color: #171617;
  display: flex;
  height: 100%;
}
.cointenerTextAbout {
  text-align: center;
  margin: auto;
  display: inline-block;
  display: grid;
  grid-template-columns: repeat(1, 60%);
  justify-content: center;
  width: 80%;
}
.photoPau {
  border-radius: 50%;
  width: 30%;
  margin: 0 auto;
  padding: 40px 0 40px 0;
}
.titleAbout {
  font-family: "Lato";
  font-size: 30px;
  color: #b3b3b3;
  padding: 0 0 20px 0;
}
.aboutMe {
  color: #b3b3b3;
  font-family: "Lato";
  font-size: 16px;
  text-align: left;
  padding: 0 0 40px 0;
}
.buttonAboutContact {
  margin: 0 5px 80px 0;
  padding: 10px;
  background: #2e2e2e;
  color: #b3b3b3;
  width: 40%;
}

.buttonAboutContact:hover {
  color: #00fa77;

}

@media only screen and (max-width: 900px) {
  .cointenerTextAbout {
    grid-template-columns: repeat(1, 80%);
  }
}

@media only screen and (max-width: 780px) {
  .titleAbout {
    font-size: 25px;
  }
  .photoPau {
    width: 50%;
  }
  .aboutMe {
    font-size: 14px;
  }
  .cointenerTextAbout {
    grid-template-columns: repeat(1, 80%);
  }
}

@media only screen and (max-width: 650px) {
  .cointenerAbout {
    display: inline-block;
    height: auto;
  }
  .cointenerTextAbout {
    display: absolute;
    width: 100%;
    grid-template-columns: repeat(1, 80%);
  }
  .photoPau {
    width: 50%;
  }
  .titleAbout {
    font-size: 25px;
  }

  .buttonAboutContact {
    margin: 0 15px 80px 0;
    padding: 10px;
    background: #2e2e2e;
    color: #b3b3b3;
    width: 45%;
  }
  @media only screen and (max-width: 370px) {
    .buttonAboutContact {
      width: 100%;
      margin: 0 5px 20px 0;
    }
    .titleAbout {
      font-size: 23px;
    }
  }
  @media only screen and (max-width: 370px) {
    .titleAbout {
      font-size: 23px;
    }
  }
}

.cointenerAllCards{
    display: inline-block;
    display: grid;
    grid-template-columns: repeat(2, 40%);
    grid-gap: 55px;
   justify-content: center; 
   margin: 0 0 80px 0;
}
.cointenerCard{
    background: black;
    width: 100%;
    height: auto;
    border-radius: 5px;
    padding: 10px 30px 10px 30px;
}
.imgCard{
    text-align: center;
    margin: 20px 0 10px 0;
}
.proyectName{
    font-size: 25px;
}
.infoProyect{
    font-family: "Lato";
    font-size: 12px;
    padding: 15px;
    color: #B3B3B3;
}
.descriptionProyect{
    margin: 0 0 10px 0;
}
.containerTagList{
    display: inline-block;
}
.technilogies{
    font-family: "Lato";
    font-style:bold;
    font-size: 18px;
    margin-bottom: 10px;
}
.tagList{
    color: #B3B3B3;
    padding: 2px 10px;
    margin: 4px;
    background: #2e2e2e;
    border-radius: 50px;
}
.cointenerButtons{
    text-align: center;
    margin: 20px 0 20px 0;
}
.buttonProyect{
    margin: 0 5px 0 0;
    padding: 10px;
    background: #171617;
    color: #B3B3B3;
    width: 48%;
}

.buttonProyect:hover{
    color: #00FA77;
    background-position: left 30%;
    -webkit-transition: all 0.75s linear;
    transition: all 0.75s linear;
}
.cointenerWorks{
    background-color: #171617;
    /*background: #131313;*/
    display: flex;
}
.cointenerCardsProyects{
    width: 80%;
}
.titleWorks{
    text-align: center;
    margin: 0;
    /*width: 100%;*/
    font-family: "Lato";
    font-size: 30px;
  color:  #b3b3b3;
    padding: 0px;
    margin: 50px 0 60px 0;
}
::-webkit-scrollbar{
    background: #2e2e2e;
    width: 8px;
}
::-webkit-scrollbar-thumb{
    background-color: #00FA77;
}
.cointenerContact {
  background: #171617;
  display: flex;
  height: 100vh;
}
.cointenerInfo{
    text-align: center;
    margin: auto;
    width: 80%;
}
.titleContact {
  font-family: "Lato";
  font-size: 30px;
  padding: 0px;
  color: #b3b3b3;
}
.spanContact{
    color: #00fa77;
}
.email{
  margin: 50px 0 50px 0;
    font-family: "Lato";
    font-size: 20px;
    color: #00fa77;
}
.cointenerButtonsContact {
    width: 100%;
    margin: auto;
  }
.buttonContact {
  background: black;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin: 20px;
}
i {
  color: #b3b3b3;
}
.buttonContact:hover{
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(70%, transparent), color-stop(0%, #00FA77));
    background-image: linear-gradient(transparent 70%, #00FA77 0%);
    background-position: left 30%;
    -webkit-transition: all 0.75s round;
    transition: all 0.75s round;
}

